const fontIcon = icon(faChevronLeft, {
  prefix: "fas",
  iconName: "chevron-left",
  styles: { backgroundColor: "#6690BC", color: "#6690BC", cursor: "pointer" }
}).html;
const getTabsNumber = jsonObject => {
  var _tabsNumber = {},
    _length,
    _objectRouter = function(obj, flag) {
      if (!flag) {
        _length = Object.keys(obj).length;
        if (i in _tabsNumber) {
          if (!{}.toString.call(_tabsNumber[i]) === "[object Array]") {
            var _t = [];
            _t.push(_tabsNumber[i]);
            _t.push(_length);
            _tabsNumber[i] = _t;
          } else {
            _tabsNumber[i].push(_length);
          }
        } else {
          _tabsNumber[i] = _length;
        }
      } else {
        for (var key in obj) {
          if ({}.toString.call(obj[key] === "[object Object]")) {
            _length = Object.keys(obj[key]).length;
            if (_tabsNumber[key]) {
              if ({}.toString.call(_tabsNumber[key]) !== "[object Array]") {
                var _t = [];
                _t.push(_tabsNumber[key]);
                _t.push(_length);
                _tabsNumber[key] = _t;
              } else {
                _tabsNumber[key].push(_length);
              }
            } else {
              _tabsNumber[key] = _length;
            }
          }
        }
      }
    },
    _subRoutine = function(arr, flag) {
      for (var _i = arr.length; _i--; ) {
        if ({}.toString.call(arr[_i]) === "[object Object]") {
          for (var _key in arr[_i]) {
            if (
              {}.toString.call(arr[_i][_key]) === "[object Object]" &&
              !_tabsNumber[_key]
            ) {
              _objectRouter(arr[_i], true);
            } else if ({}.toString.call(arr[_i][_key]) === "[object Array]") {
              if (!_tabsNumber[_key]) {
                _tabsNumber[_key] = 0;
              }
              _subRoutine(arr[_i][_key], true);
            }
          }
        }
      }
    };
  for (var i in jsonObject) {
    if (Object.prototype.toString.call(jsonObject[i]) === "[object Object]") {
      _objectRouter(jsonObject[i]);
    } else if ({}.toString.call(jsonObject[i]) === "[object Array]") {
      if (_tabsNumber[i]) {
        var _t = [];
        _t.push(_tabsNumber[i]);
        _t.push(0);
        _tabsNumber[i].push(_t);
      } else {
        _tabsNumber[i] = 0;
      }
      _subRoutine(jsonObject[i]);
    }
  }
  return _tabsNumber;
};

const getTasksNames = jsonObject => {
  var _tasksNames = [],
    subRoutineArr = [],
    _subRoutine = function(arr, flag, _flag) {
      for (var i = 0; i < arr.length; i++) {
        if ({}.toString.call(arr[i]) === "[object Object]") {
          for (var j in arr[i]) {
            if (
              j &&
              {}.toString.call(j) === "[object String]" &&
              {}.toString.call(arr[i][j]) === "[object Object]"
            ) {
              if (flag) {
                if (!_t) {
                  var _t = [];
                  _t.push(j);
                } else if (_t) {
                  _t.push(j);
                }
                if (i === arr.length - 1 && _flag) {
                  subRoutineArr.push(
                    subRoutineArr.splice(subRoutineArr.length - 1, 1)
                  );
                  subRoutineArr[subRoutineArr.length - 1].push(_t);
                } else if (i === arr.length - 1) {
                  subRoutineArr.push(_t);
                }
              } else {
                subRoutineArr.push(j);
              }
            } else if (
              {}.toString.call(j) === "[object String]" &&
              {}.toString.call(arr[i][j]) === "[object Array]"
            ) {
              //typeof subRoutineArr[subRoutineArr.length - 1];
              subRoutineArr.push(j);
              if (
                typeof subRoutineArr[subRoutineArr.length - 1] === "string" &&
                typeof subRoutineArr[subRoutineArr.length - 2] === "string"
              ) {
                _subRoutine(arr[i][j], true, true);
              } else {
                _subRoutine(arr[i][j], true);
              }
            }
          }
        }
      }
    };
  for (var i in jsonObject) {
    if ({}.toString.call(i) === "[object String]") {
      if ({}.toString.call(jsonObject[i]) === "[object Object]") {
        _tasksNames.push(i);
      } else if ({}.toString.call(jsonObject[i]) === "[object Array]") {
        var t = [];
        t.push(i);
        _subRoutine(jsonObject[i]);
        t.push(subRoutineArr);
        _tasksNames.push(t);
        subRoutineArr = [];
      }
    }
  }
  return _tasksNames;
};

const initClient = ({
  fetchTasksJSON,
  userExportable,
  changeSignedInState,
  ref,
  setToastMessage,
  setToastState,
  isSignedIn,
  tasksJSON
}) => {
  window.gapi.client
    .init({
      discoveryDocs: config.DISCOVERY_DOCS,
      clientId: config.CLIENT_ID,
      scope: config.SCOPES,
      fetch_basic_profile: true,
      immediate: false /* ,
  		  prompt : 'select_account'
  		  this does not belong:
  		  read more -> https://github.com/google/google-api-javascript-client/issues/299
  		  */
    })
    .then(async function() {
      // Handle the initial sign-in state.
      if (isSignedIn) {
        console.log(
          "%cinitClient invocation -> already signed in to your Google Account",
          "background: maroon; color: yellow; font-size: 18px;"
        );
        var user = await window.gapi.auth2.getAuthInstance().currentUser.get();
        var profile = user.getBasicProfile();
        var _userExportable = {};
        _userExportable["email"] = profile.getEmail();
        _userExportable["fullName"] = profile.getName();
        _userExportable["imageURL"] = profile.getImageUrl();
        userExportable(_userExportable);
        setToastState(true);
        setToastMessage(
          "You are currently signed in to your Google account: " +
            _userExportable["fullName"]
        );
      } else {
        handleAuthentication();
        // Listen for sign-in state changes.
        window.gapi.auth2.getAuthInstance().isSignedIn.listen(
          updateSigninStatus.bind(null, {
            isSignedIn: null,
            fetchTasksJSON,
            changeSignedInState,
            userExportable,
            ref,
            setToastMessage,
            setToastState,
            tasksJSON
          })
        );
        if (
          window.gapi &&
          window.gapi.auth2 &&
          window.gapi.auth2.getAuthInstance().isSignedIn.get()
        ) {
          updateSigninStatus({
            isSignedIn: window.gapi.auth2.getAuthInstance().isSignedIn.get(),
            fetchTasksJSON,
            changeSignedInState,
            userExportable,
            ref,
            setToastMessage,
            setToastState,
            tasksJSON
          });
        }
        //signoutButton.onclick = handleSignoutClick;
      }
    })
    .then(() => {}, function(err) {
      setToastState(true);
      setToastMessage(err.message);

      console.log(
        "%c ERROR from gApi initClient: " + err.message,
        "background: maroon; color: white; font-size: 40px;"
      );
    });
};

const updateSigninStatus = async ({
  isSignedIn,
  fetchTasksJSON,
  userExportable,
  changeSignedInState,
  ref,
  setToastMessage,
  setToastState
}) => {
  var user = await window.gapi.auth2.getAuthInstance().currentUser.get();
  var profile = user.getBasicProfile();
  var _userExportable = {};
  _userExportable["email"] = profile.getEmail();
  _userExportable["fullName"] = profile.getName();
  _userExportable["imageURL"] = profile.getImageUrl();
  userExportable(_userExportable);
  setToastState(true);
  setToastMessage(
    "You are currently signed in to your Google account: " +
      _userExportable["fullName"]
  );
  const tasksResponse = await getFileByName.call(this, "tasks.json");
  fetchTasksJSON(tasksResponse.result);
  changeSignedInState(true);
};

const onTasksJSONFetched = (jsonObject, ref) => {
  var tabsNumberByKey = getTabsNumber(jsonObject),
    tasksNumber = (function() {
      var returnable = 0;
      for (var i in tabsNumberByKey) {
        if (i && {}.toString.call(tabsNumberByKey[i]) === "[object Number]") {
          returnable += 1;
        } else if (
          i &&
          {}.toString.call(tabsNumberByKey[i]) === "[object Array]"
        ) {
          returnable += tabsNumberByKey[i].length;
        }
      }
      return returnable;
    })(),
    taskNames = getTasksNames(jsonObject),
    accordeon = new Accordeon(
      $(ref.current), //$(".accordeon-wrapper"),
      tasksNumber,
      30,
      tabsNumberByKey
    ),
    text,
    tabsNames = [],
    eventName;
  if ("ontouchstart" in document) {
    eventName = "touchstart";
  } else {
    eventName = "click";
  }
  accordeon.build(taskNames);
  $(".title").on(eventName, function(e) {
    $(document).trigger("instance.u");
    tabsNames = [];
    $("body").trigger("title-change");
    if (
      $(".details-body-code")[0][
        "innerText" in $(".details-body-code")[0] ? "innerText" : "innerHTML"
      ]
    ) {
      $(".details-body-code")[0][
        "innerText" in $(".details-body-entrails")[0]
          ? "innerText"
          : "innerHTML"
      ] = "";
    }
    if (
      $(".details-body-description")[0][
        "innerText" in $(".details-body-code")[0] ? "innerText" : "innerHTML"
      ]
    ) {
      $(".details-body-description")[0][
        "innerText" in $(".details-body-code")[0] ? "innerText" : "innerHTML"
      ] = "";
    }
    if (e.target.tagName === "DIV") {
      text =
        e.target.firstChild[
          "innerText" in e.target.firstChild ? "innerText" : "innerHTML"
        ];
      if (text) {
        $(e.target).prepend(
          $("<div>", {
            id: "clickableTitleArrow",
            html: "&#10147;",
            style: "display : inline; margin-left: 5%; font-size: 17px;"
          })
        );
        $("#clickableTitleArrow")
          .parent()
          .css("color", "#fff");
        $("#clickableTitleArrow")
          .parent()
          .css("background-color", "#C51F00");
      }
    } else {
      text =
        e.target[
          "innerText" in e.target.firstChild ? "innerText" : "innerHTML"
        ];
      if (text && tabsNumberByKey[text]) {
        $(e.target.parentNode).prepend(
          $("<div>", {
            id: "clickableTitleArrow",
            html: "&#10147;",
            style: "display : inline; margin-left: 5%; font-size: 17px;"
          })
        );
        $("#clickableTitleArrow")
          .parent()
          .css("color", "#fff");
        $("#clickableTitleArrow")
          .parent()
          .css("background-color", "#C51F00");
      }
    }
    if (text in tabsNumberByKey && tabsNumberByKey[text]) {
      $(".header .columniser-text").css("visibility", "visible");
      if (
        Object.prototype.toString.call(jsonObject[text]) === "[object Object]"
      ) {
        for (var j in jsonObject[text]) {
          tabsNames.push(j);
        }
      } else {
        var _subRoutine = function(arr) {
          for (var i = arr.length; i--; ) {
            if ({}.toString.call(arr[i]) === "[object Object]") {
              for (var k in arr[i]) {
                if ({}.toString.call(arr[i][k]) === "[object Object]") {
                  if (k === text) {
                    for (var _k in arr[i][k]) {
                      tabsNames.push(_k);
                    }
                  }
                } else if ({}.toString.call(arr[i][k]) === "[object Array]") {
                  _subRoutine(arr[i][k]);
                }
              }
            }
          }
        };
        for (var q in jsonObject) {
          if ({}.toString.call(jsonObject[q]) === "[object Array]") {
            _subRoutine(jsonObject[q]);
          }
        }
      }
      $(".tabulator-wrapper").remove();
      $(".arrow-left").off(eventName);
      $(".arrow-right").off(eventName);
      $(".tabs-li").off(eventName);
      accordeon.tabulator(tabsNumberByKey[text], tabsNames, jsonObject, text);
      $(".columniser-text")
        .first()
        .append(
          $("<div></div>", {
            style: "float: left;",
            class: "breadcrumb"
          }).append(
            $("<span></span>", {
              style:
                "font-family: 'Segoe UI'; font-size: 14px; cursor: pointer; color:#6690BC;"
            }).text(
              $(".columniser-text")
                .first()
                .text()
            )
          )
        );
      $(".columniser-text")
        .first()
        .html(
          $(".columniser-text")
            .first()
            .html()
            .replace(
              $(".columniser-text")
                .first()
                .html()
                .match(/^(.*)<div/)[1],
              ""
            )
        );
      $(".columniser-text:first div").prepend(fontIcon);
      $(".titles-column-left").css("display", "none");
      $(".columniser-text")
        .has(".breadcrumb")
        .on("click", function(e) {
          if (!$(".header .columniser-text").length) {
            $(".header").append($(".columniser-text").has(".breadcrumb"));
          }
          $(document).trigger("instance.u");
          $(this).css("visibility", "hidden");
          $(".titles-column-left").css("display", "");
          $(".columniser-text")
            .has(".tabulator-wrapper")
            .remove();
          $(".scrolling-left").remove();
          $(".scrolling-right").remove();
          $(".details-body-code").html("");
          $(".details-body-description").html("");
        });
    }
    if (tabsNumberByKey[text] === 0 || !tabsNumberByKey[text]) {
      $(".tabulator-wrapper").remove();
      $(".arrow-left").off(eventName);
      $(".arrow-right").off(eventName);
      $(".tabs-li").off(eventName);
      $(".scrolling-left").css("display", "none");
      $(".scrolling-right").css("display", "none");
    }
  });
};

const gapiScriptLoader = ({
  isSignedIn,
  userExportable,
  changeSignedInState,
  fetchTasksJSON,
  ref,
  setToastMessage,
  setToastState,
  tasksJSON
}) => {
  const gapiScript = document.createElement("script");
  gapiScript.src = "https://apis.google.com/js/api.js?onload=onGapiLoad";
  gapiScript.id = "gapiScript";
  var onGapiLoad = function() {
    window.gapi &&
      window.gapi.load(
        "client:auth2",
        initClient.bind(null, {
          fetchTasksJSON,
          userExportable,
          changeSignedInState,
          ref,
          setToastMessage,
          setToastState,
          isSignedIn,
          tasksJSON
        })
      );
  };
  window.onGapiLoad = onGapiLoad;
  document.body.appendChild(gapiScript);
};

const App = () => {
  const ref = React.createRef();
  const [isSignedIn, changeSignedInState] = useLocalStorage(
    "isSignedIn",
    false
  ); //useState(false);
  const [signedInUser, userExportable] = useLocalStorage("signedInUser", {
    email: "",
    fullName: "",
    imageURL: ""
  }); //useState([]);

  const [tasksJSON, fetchTasksJSON] = useLocalStorage("tasksJSON", {});

  const [toastMessage, setToastMessage] = useLocalStorage(
    "toastMessage",
    "You are currently not signed in to your Google account: pls., sign in"
  );
  const [isToast, setToastState] = useLocalStorage("isToast", false);

  useEffect(() => {
    gapiScriptLoader.call(null, {
      isSignedIn,
      userExportable,
      changeSignedInState,
      fetchTasksJSON,
      ref,
      setToastMessage,
      setToastState,
      tasksJSON
    });
    if (isSignedIn && Object.keys(tasksJSON).length) {
      onTasksJSONFetched(tasksJSON, ref);
      $("document").ready(function() {
        $("body").on("posting", function(e, _text, description) {
          $(".details-body-entrails").css("display", "none");
          if (description.match(/code/i)) {
            $(".details-body-description")[0][
              "innerText" in $(".details-body-code")[0]
                ? "innerText"
                : "innerHTML"
            ] = "";
            $(".details-body-entrails").css("display", "");
            $(".details-body-code")[0][
              "innerText" in $(".details-body-code")[0]
                ? "innerText"
                : "innerHTML"
            ] = _text;
          } else {
            $(".details-body-description")[0][
              "innerText" in $(".details-body-code")[0]
                ? "innerText"
                : "innerHTML"
            ] = "";
            $(".details-body-description").append(_text);
          }

          if ($(".prettyprinted").length) {
            $(".prettyprinted").removeClass("prettyprinted");
          }
          window.prettyPrint();
        });
      });
    }
  }, [isSignedIn]);

  useEffect(() => {
    $(".toast").html(toastMessage);
  }, [toastMessage]);

  return (
    <Router>
      <div ref={ref} className="main-wrapper">
        {/* <Switch> */}
        {/* <Route
        path="/asana-gapi/:fileName"
        render={({ match }) => {
          console.log(
            ' path="/" render-1',
            match,
            match.params.fileName,
            "match.params.fileName"
          );
          return (
            <div>
              <HeaderStrip />
              <FileExported />
            </div>
          );
        }}
      /> */}
        <Route
          path="/"
          render={() => {
            console.log(
              '%c path="/" Route :: main wrapper',
              "background: green; color: white;"
            );
            return (
              // <div className="accordeon-wrapper">
              //   123123123123
              //   {/* <HeaderStrip />
              // <FileExported /> */}
              // </div>
              <div>
                {/* <div className="header">
                  <div className="h">JavaScript Snippets and Code Examples</div>
                </div> */}
                <div className="accordeon-wrapper"></div>
                <div className="footer"></div>
              </div>
            );
          }}
        />
        {/* </Switch> */}
      </div>
    </Router>
  );
};

export default App;
