import { useState } from "react";

export const getAllFolders = async () => {
  return await window.gapi.client.drive.files
    .list({
      q: "mimeType = 'application/vnd.google-apps.folder'",
      fields: "nextPageToken, files(id, name, parents)"
    })
    .then(res => {
      return res.result.files;
    });
};

var allFoldersList = null;
var ID = null;

export const getAllFilesByAName = async name => {
  return await window.gapi.client.drive.files
    .list({
      q: `name="${name}"`,
      pageSize: 1000,
      fields:
        "nextPageToken, files(id, name, webContentLink, webViewLink, parents)",
      orderBy: "createdTime"
    })
    .then(res => {
      return res.result.files;
    });
};

export const matchTheFileByUrlFromEponymous = async (
  EponymousCollection,
  url
) => {
  EponymousCollection.some(({ id, name, parents }) => {
    let nextParent = "";
    let _url = "";
    let factor = url.match(/[/]/g).length - 1;
    _url = "/" + name + _url;
    while (factor--) {
      if (!nextParent) {
        nextParent = parents[0];
      }
      let parent = allFoldersList.find(p => {
        return p.id == nextParent;
      });
      if (parent) {
        nextParent = parent.parents[0];
        _url = "/" + parent.name + _url;
      }
    }
    if (url.match(_url)) {
      ID = id;
      console.log(
        "%c match! -> url: " + url + " matched _url: " + _url,
        "background: maroon; color:yellow;"
      );
      return true;
    }
  });
};

export const getFileByName = async (name, url) => {
  if (!allFoldersList) {
    allFoldersList = await getAllFolders();
  }
  const file_s_ByName = await getAllFilesByAName(name);
  if (file_s_ByName.length > 1 && url) {
    matchTheFileByUrlFromEponymous(file_s_ByName, url);
  } else {
    const file = file_s_ByName.find(f => f.name == `${name}`);
    if (file && file.name && /jpg|png|jpeg|PNG|JPG|JPEG/.test(file.name)) {
      return file;
    }
    ID = file.id;
  }
  const fileContent = await window.gapi.client.drive.files.get({
    fileId: ID,
    alt: "media"
  });
  return fileContent;
};

